import React from 'react'
import Editor from 'components/Editor'
import testThoughts from './thoughts/editorComponent.product.thoughts'

const Thoughts = () => (
  <div>
    THOUGHTS
    <Editor
      text={testThoughts}
      view={'eagle'}
      editing={false}
      commenting={false}
    />
  </div>
)

export default Thoughts



