import React from 'react'
import store, { history } from './store'
import './App.css'

import { Provider } from 'react-redux'
import { ConnectedRouter } from 'react-router-redux'
import { Route, Switch } from 'react-router-dom'

import {
    ApolloClient,
    ApolloProvider,
    createNetworkInterface,
} from 'react-apollo';

import Home from './pages/Home'
import Thoughts from './pages/Thoughts'
import Stories from './pages/Home'
import Timeline from './pages/Home'
import Legal from './pages/Legal'
import NotFound from './pages/Home'

// Todo: replace uri it with config variable. For local: use /graphql, For prod: use aws api gateway url
const client = new ApolloClient({
  networkInterface: createNetworkInterface({ uri: 'http://localhost:4000/graphql' }),
})

const App = () => (
  <ApolloProvider client={client}>
    <Provider store={store}>
      <ConnectedRouter history={history}>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/thoughts" component={Thoughts} />
          <Route exact path="/stories" component={Stories} />
          <Route exact path="/timeline" component={Timeline} />
          <Route exact path="/legal" component={Legal} />
          <Route exact path="*" component={NotFound} />
        </Switch>
      </ConnectedRouter>
    </Provider>
  </ApolloProvider>
)

export default App



