import { createStore, applyMiddleware, compose } from 'redux'
import { routerMiddleware } from 'react-router-redux'
import thunk from 'redux-thunk'
import createBrowserHistory from 'history/createBrowserHistory'
import makeRootReducer from './makeRootReducer'

export const history = createBrowserHistory()

// Initial state is blank unless window.__INITIAL_STATE__ is set
const initialState = {
  ...window.__INITIAL_STATE__,
}

// Add thunk and router middleware
const middleware = [
  thunk,
  routerMiddleware(history),
]

// Init enhancers
const enhancers = []

// Add redux dev tools extension if in development
if (process.env.NODE_ENV === 'development') {
  const devToolsExtension = window.devToolsExtension

  if (typeof devToolsExtension === 'function') {
    enhancers.push(devToolsExtension())
  }
}

// Chain together middleware and enhancers
const composedEnhancers = compose(
  applyMiddleware(...middleware),
  ...enhancers
)

// Set store
const store = createStore(
  makeRootReducer(),
  initialState,
  composedEnhancers,
)

// Set async reducers for async reducer injection
store.asyncReducers = {}

export default store



