export default `

Editor Component Views:
- Mobile (not recommended for editing)
- Tablet (not recommended for editing)
- Eagle View Desktop (zoomed out)
- Normal View Desktop (google docs-ish zoomed in)
- Pretty View Desktop (medium style zoomed in & pretty)

<Editor
  view={String}          // must be one of 'mobile', 'tablet', 'eagle', 'docs', 'pretty'
  text={String}          // from string literal
  editing={Boolean}      // when the author is editing
  commenting={Boolean}   // can only be true when editing is false
/>

`


