import React, { Component } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import { getSelectedText } from 'lib'

const EditorWrapper = styled.div`
  font-family: 'Droid Sans Mono', monospace;
  padding: 30px;
  // background: ${props => props.theme.color};
  // border-radius: 3px;
  // border: none;
  // color: black;
  // text-align: center;
`

function handleMouseUp({ target }) {
  const selectedText = getSelectedText()
  console.log('target', target)
  console.log('selectedText', selectedText)
}

function onCopy() {
  alert('Please be sure that you have gotten permission to copy')
}

const TemplateLiteralDisplay = (jsString) => {
  return jsString.split('\n').map((item, key) => {
    return (
      <span key={key} onCopy={onCopy} onCut={onCopy} onMouseUp={(e) => handleMouseUp(e)}>
        {item.replace(/ /g, "\u00a0")}
        <br/>
      </span>
    )
  })
}

export default class Editor extends Component {
  static propTypes = {
    text: PropTypes.string,
    view: PropTypes.oneOf(['mobile', 'tablet', 'eagle', 'docs', 'pretty']).isRequired,
    editing: PropTypes.bool,
    commenting: PropTypes.bool,
  }

  render() {
    return (
      <EditorWrapper>
        {TemplateLiteralDisplay(this.props.text)}
      </EditorWrapper>
    )
  }
}



